.visually-hidden {
  position: absolute;
  top: auto;
  left: -9999em;
  overflow: hidden;
  margin: 0;
  padding: 0;
  width: 1px;
  height: 1px;
}

.focusable.visually-hidden:active, .focusable.visually-hidden:focus {
  position: static;
  overflow: visible;
  clip: auto;
  width: auto;
  height: auto;
}

@media (min-width: 300px) {
  .bp-foo {
    width: 100%;
  }
}

@supports (display: grid) {
  .sg-container {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-gap: 0.25fr;
    padding-left: 0;
    padding-right: 0;
  }
}

* {
  box-sizing: border-box;
}

.App {
  display: flex;
  flex: 1;
  flex-direction: column;
  min-height: 100vh;
}

.App .Header {
  flex: 0;
}

.App .Main {
  flex: 1 1 auto;
}

.paper {
  max-width: 800px;
  margin: 0 auto;
}

.Main {
  background-color: #808080;
}

body {
  background-color: #fff;
  color: #2196F3;
